import axios from 'axios'
import firebase from 'firebase/app'

function stringToBoolean(str) {
    switch (str.toLowerCase()) {
        case "true": case "yes": case "1": return true;
        case "false": case "no": case "0": case null: return false;
        default: return Boolean(str);
    }
}

function emailValidVerify(email) {
    return new Promise((resolve, reject) => {
        let whiteList = ["test-", "@tunekey.test", ".test", "@tk.com", "@tunekey.com", "@tunkey.com"]
        whiteList.forEach(item => {
            if (email.indexOf(item) >= 0) {
                resolve(true)
                return
            }
        })
        let url = "https://emailverification.whoisxmlapi.com/api/v1?apiKey=at_wU22I8A8DpX6v2sXO1XT3hlEBdFtG&emailAddress=" + email
        axios.get(url)
            .then(res => {
                if (res.status) {
                    if (res.status === 200) {
                        let data = res.data
                        if (data) {
                            console.log('[Email校验] => 校验结果: ', JSON.stringify(data));
                            resolve(stringToBoolean(data.smtpCheck))
                            return
                        }
                    }
                }
                resolve(false)
            })
            .catch(err => {
                console.error(err);
                reject(err)
            })
    })
}

function userId() {
    const userString = localStorage.getItem('auth_user')
    if (userString !== '') {
        let user = JSON.parse(userString)
        if (user) {
            return user.uid
        }
    }
    return ''
}

function objEquals(obj, target) {
    var props1 = Object.getOwnPropertyNames(obj);
    var props2 = Object.getOwnPropertyNames(target);
    if (props1.length != props2.length) {
        return false;
    }
    for (var i = 0, max = props1.length; i < max; i++) {
        var propName = props1[i];
        if (this[propName] !== obj[propName]) {
            return false;
        }
    }
    return true;
}

function userAvatarPath(userId) {
    return 'https://firebasestorage.googleapis.com/v0/b/tunekey-2019.appspot.com/o/images%2Fuser_avatars%2F' + userId + '.jpg?alt=media&token=42b79c74-4e37-4207-8591-efea10dd794d'
}

let commons = {
    emailValidVerify,
    userId,
    objEquals,
    userAvatarPath
}

export default commons